var SafeEventEmitter = require('safe-event-emitter');

var DuplexStream = require('readable-stream').Duplex;

module.exports = createStreamMiddleware;

function createStreamMiddleware() {
  var idMap = {};
  var stream = new DuplexStream({
    objectMode: true,
    read: readNoop,
    write: processMessage
  });
  var events = new SafeEventEmitter();

  var middleware = function middleware(req, res, next, end) {
    // write req to stream
    stream.push(req); // register request on id map

    idMap[req.id] = {
      req: req,
      res: res,
      next: next,
      end: end
    };
  };

  return {
    events: events,
    middleware: middleware,
    stream: stream
  };

  function readNoop() {
    return false;
  }

  function processMessage(res, encoding, cb) {
    var err;

    try {
      var isNotification = !res.id;

      if (isNotification) {
        processNotification(res);
      } else {
        processResponse(res);
      }
    } catch (_err) {
      err = _err;
    } // continue processing stream


    cb(err);
  }

  function processResponse(res) {
    var context = idMap[res.id];
    if (!context) throw new Error("StreamMiddleware - Unknown response id ".concat(res.id));
    delete idMap[res.id]; // copy whole res onto original res

    Object.assign(context.res, res); // run callback on empty stack,
    // prevent internal stream-handler from catching errors

    setTimeout(context.end);
  }

  function processNotification(res) {
    events.emit('notification', res);
  }
}